.toastContainer {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-width: 400px;
  width: 100%;
}

@media (max-width: 640px) {
  .toastContainer {
    top: 10px;
    right: 10px;
    left: 10px;
    max-width: none;
  }
}
